<template>
  <div class="gonggao-container">
      <div class="txt-list clearfix">
        <!-- 文本 -->
        <div class="txt-box clearfix">
          <div class="content">
            <div class="title">
              <span class="txt">公告</span>
            </div>
            <div class="comment">
              <span class="text">{{gonGaoTxt}}</span>
            </div>
            <!-- <img class="img" src="static/img/test4.jpg"> -->
          </div>
        </div>
      </div>
    </div>
</template>
<style lang="less">
  @import "../../../static/css/common.less";
  .gonggao-container{
    background: linear-gradient(#bde2e3, #fff); /* 标准的语法 */
    .txt-list{
      box-shadow: 0 0 0.2rem rgba(0,0,0,.1);
      width: 100%;
      margin-bottom: .4rem;
      background: url('../../../static/img/notice-left.png') no-repeat,
                  url('../../../static/img/notice-right.png') no-repeat right bottom;
      .txt-box{
        width: 100%;
        padding: .2rem .3rem;
        border-bottom: 1.5/75rem solid @lineLightColor;
        display: flex;
        flex-direction: column;
        .content{
          margin-top: .1rem;
          line-height: .4rem;
          display: flex;
          justify-content: space-around;
          align-items: center;
          padding: .2rem;
          border-radius: .1rem;
          background: rgba(5, 5, 5, 0.295);
          color: #fff;
          .title{
            height: 1.2rem;
            padding-right: .2rem;
            font-size: 16px;
            font-weight: bold;
            letter-spacing: .05rem;
            writing-mode: vertical-lr;
            text-align: center;
            border-right: 1px solid @light-gray;
          }
          .comment{
            width: 100%;
            height: 1.2rem;
            font-size: 0;
            line-height: .6rem;
            margin-left: .2rem;
            display: flex;
            align-items: center;
            .text{
              font-size: .38rem;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
          }
        }
      }
    }
  }
</style>
<script>
export default{
  props:['gonGaoTxt'],
  data(){
    return{
    }
  },
}
</script>



