<template>
  <transition name="animate-toBottom">
    <div class="toast-container" v-if="visible">
      <div class="txt">
        {{message}}
      </div>
    </div>
  </transition>
</template>
<style lang="less">
  @import "../../../static/css/common.less";
  .toast-container{
    display: flex;
    position: fixed;
    width:100%;
    justify-content: center;
    top:4rem;
    .txt{
      max-width: 80%;
      padding: .2rem .5rem;
      text-align: center;
      font-size: .5rem;
      line-height: .7rem;
      background: -webkit-linear-gradient(left, rgba(24, 4, 4, 0.9) , rgba(80, 9, 9, 0.8) , rgba(24, 4, 4, 0.9) ); /* Safari 5.1 - 6.0 */
      background: -o-linear-gradient(right, rgba(24, 4, 4, 0.9) , rgba(80, 9, 9, 0.8) , rgba(24, 4, 4, 0.9) ); /* Opera 11.1 - 12.0 */
      background: -moz-linear-gradient(right, rgba(24, 4, 4, 0.9) , rgba(80, 9, 9, 0.8) , rgba(24, 4, 4, 0.9) ); /* Firefox 3.6 - 15 */
      background: linear-gradient(to right, rgba(24, 4, 4, 0.9) , rgba(80, 9, 9, 0.8) , rgba(24, 4, 4, 0.9) ); /* 标准的语法 */
      color: #fff;
      border-radius: .1rem;
      box-shadow: 0 0 .5rem rgba(0, 0, 0, 0.322);
    }
  }
</style>
<script>
export default{
  data(){
    return{
      visible: false,
      message:'',
    }
  }
}
</script>



