// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'


//================首页====================
import shouYe from './components/shouYe/home.vue'
import homeTabOne from './components/shouYe/homeTabOne.vue'//心灵走廊
import homeTabTwo from './components/shouYe/homeTabTwo.vue'//话题讨论
import homeTabThree from './components/shouYe/homeTabThree.vue'//最近精句


//================句子====================
import juZi from './components/juZi/juZi.vue'
import jzReMen from './components/juZi/juZiTabOne.vue'//最近热门
import jzZuiXin from './components/juZi/juZiTabTwo.vue'//最新发布
import jzPaiHang from './components/juZi/juZiTabThree.vue'//所有排行
import jzFenLei from './components/juZi/juZiTabFour.vue'//所有分类
//========================================


//================树洞====================
import shuDong from './components/shuDong/shuDong.vue'
import xiaoJiuJiu from './components/shuDong/shuDongTabOne.vue'//小九九
import tuCaoTaoLun from './components/shuDong/shuDongTabTwo.vue'//所有分类
//========================================

//告白测试
import gaoBai from './components/common/gaoBai.vue'

//================我的====================
import woDe from './components/woDe/woDe.vue'
//========================================


//=============手指操作插件================
import AlloyFinger from 'alloyfinger'
import AlloyFingerPlugin from 'alloyfinger/vue/alloy_finger.vue'
//========================================


//============自定义全局提示插件============
//toast组件
import Toast from '../static/js/toast'
//Confirm组件
import Confirm from '../static/js/confirm'
//loading组件
import Loading from '../static/js/loading'
//========================================



Vue.use(AlloyFingerPlugin, {
    AlloyFinger
});
Vue.use(VueRouter);
Vue.use(Toast);
Vue.use(Confirm);
Vue.use(Loading);
Vue.config.productionTip = false

// ============ 定义路由 ==================
// 底部菜单
const routesMenu = [
  //=======首页=======
  { path: '/shouYe',component: shouYe,
    children:[
      { path: '/xinLingZouLang', component: homeTabOne },//心灵走廊
      { path: '/huaTiTaoLun',component: homeTabTwo },//话题讨论
      { path: '/zuiJinJingJu',component: homeTabThree },//最近精句
      { path: '/' ,component: homeTabOne},//默认打开home(重定向)
    ]
  },
  //========句子========
  { path: '/juZi',component: juZi ,
    children:[
      { path: '/jzReMen', component: jzReMen },//心灵走廊
      { path: '/jzZuiXin', component: jzZuiXin },//心灵走廊
      { path: '/jzPaiHang', component: jzPaiHang },//心灵走廊
      { path: '/jzFenLei', component: jzFenLei },//心灵走廊
      { path: '/', component: jzReMen },//心灵走廊
    ]
  },
  //========树洞========
  { path: '/shuDong',component: shuDong  ,
    children:[
      { path: '/xiaoJiuJiu', component: xiaoJiuJiu },//小九九
      { path: '/tuCaoTaoLun', component: tuCaoTaoLun },//吐槽讨论
      { path: '/', component: xiaoJiuJiu },//心灵走廊
    ]
  },
  //========我的========
  { path: '/woDe',component: woDe },
  { path: '/gaoBai',component: gaoBai },
  //默认打开home(重定向)
  { path: '/' ,component: shouYe,
    children:[
      { path: '/' ,component: homeTabOne},//默认打开homeTabOne(重定向)
    ]
  },
]

// 创建路由实例 底部菜单
const routerMenu = new VueRouter({
  routes:routesMenu,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
     return savedPosition
    } else {
     return { x: 0, y: 0 }
    }
  }
})

// 创建和挂载根实例
new Vue({
  el:'#app',
  router: routerMenu,
  template: '<App/>',
  components: { App }
});




