<template>
<!-- 进入退出动画和快速设置一样 -->
  <transition name="animate-toTop">
    <div class="search-panel">
      <div class="search-input">
        <div class="input-box">
          <input class="input" ref="inputTxt" v-model="searchTxt" type="text" placeholder="请输入搜索内容">
          <transition name="animate-toTop">
            <div v-show="quickRes" class="quick-res">
              <ul class="qs-ul">
                <li class="list">{{searchTxt}}</li>
                <li class="list">{{searchTxt}}</li>
                <li class="list">{{searchTxt}}</li>
                <li class="list">{{searchTxt}}</li>
              </ul>
            </div>
          </transition>
        </div>
        <transition name="animate-opacity">
          <div  v-show="deletIcon"class="delet-box" @click="deletTxt()">
            <i class="close icon-add-new"></i>
          </div>
        </transition>
      </div>
      <div class="search-res">
        搜索结果
      </div>
    </div>
  </transition>
</template>
<style lang="less">
  @import "../../../static/css/common.less";
  .search-panel{
    position: fixed;
    height: 100%;
    width: 100%;
    background: rgba(255, 255, 255, 0.95);
    z-index: @zSearchPanel;
    .search-input{
      position: absolute;
      width: 5.5rem;
      height: 60/75rem;
      display: inline-block;
      margin-left: 1.8rem;
      top:-78/75rem;
      .input-box{
        width: 5.5rem;
        height: 60/75rem;
        text-indent: 0.3rem;
        border: none;
        border-radius: 2.5rem;
        background: #fff;
        .input{
          width: 4rem;
          height: 60/75rem;
          border-radius: 0.1rem;
          border: none;
        }
        .quick-res{
          width: 95%;
          margin:0.01rem auto 0;
          box-shadow: 0 0.1rem 0.2rem rgba(0, 0, 0, 0.1);
          border-radius: 0.1rem 0.1rem 0.3rem 0.3rem;
          background: rgba(255,255,255,0.9);
          .qs-ul{
            .list{
              height: 1.2rem;
              line-height: 1.2rem;
              columns: @main-black;
              transition: @animateTime;
              padding-right: 0.3rem;
              overflow: hidden;
              text-overflow:ellipsis;
              white-space: nowrap;
              &:not(:last-child){
                border-bottom: 1px solid @lineColor;
              }
              &:active{
                background: @lineColor;
              }
            }
          }
        }
      }
      .delet-box{
        position: absolute;
        top:0rem;
        right: 0rem;
        border-radius: 0 50% 50% 0;
        width: 1rem;
        height: 60/75rem;
        border-left: 1px solid @lineColor;
        background: #fff;
        &:active{
          .close{
            color: @main-dkred;
          }
        }
        .close{
          line-height: 60/75rem;
          color:@main-red;
          font-size: 20px;
          right: 0.3rem;
          position: absolute;
          transform: rotate(45deg);
        }
      }
    }
  }
</style>
<script>
  export default {
    // 父组件传的值 搜索界面是否显示的true false
    props:['searchPanel','searchClick'],

    data(){
      return{
        searchTxt:'',//搜索输入值
        deletIcon:false,//输入框删除按钮
        quickRes:false,//快速结果列表
      }
    },

    methods:{
      deletTxt(){//点击删除红叉
        this.searchTxt = "";//文本清空
        this.deletIcon = false;//删除红叉
        this.$refs.inputTxt.focus();//自动聚焦文本框
        this.quickRes = false;//快速结果删除
      }
    },

    watch:{
      //监听输入框长度，判断红色X是否出现
      searchTxt:function(val){
        if(val.length > 0){
          this.deletIcon = true;//显示红叉
          this.quickRes = true;//快速结果显示
        }else{
          this.deletIcon = false;//删除红叉
          this.quickRes = false;//快速结果删除
        }
      },
      //监听本组建是否显示 进行清空搜索框和自动聚焦
      searchPanel:function(val){
        if(val === false){//不显示时
          this.searchTxt = "";//文本清空
        }else{
          this.$refs.inputTxt.focus();//自动聚焦文本框
        }
      },
      //监听 确认搜索 按钮
      searchClick:function(val){//监听本组建是否显示
        if(val === true){//不显示时
          this.quickRes = false;//隐藏快速结果列表
          alert("搜索："+this.searchTxt)
          //进行搜索操作
          // 还原searchClick 为 false
          // console.log('父组件搜索请求');
          this.$emit('searchClickFalse');
          // console.log('搜索完成，重置搜索');
        }
      },

    },

  }
</script>



