<template>
  <transition name="leftLeave">
    <div class="left-container" v-finger:press-move="pressMove" v-finger:touch-end="touchEnd" :style="{transform:translateX,transition:animateTime}">
      <div class="left" ref="leftPanel" :style="{transform:translateX,transition:animateTime}">
        <!-- 上部分 -->
        <div class="header">
          <!-- 状态栏 -->
          <div class="nav-bar">
          </div>
          <!-- 编辑 -->
          <div class="edit">
            <span class="edt-icon">
              <i class="icon-fankui"></i>
            </span>
          </div>
          <!-- 头像 -->
          <div class="head-box">
            <div class="head-img">
              <img class="u-img" src="../../../static/img/test.jpg" alt="">
            </div>
            <div class="txt">
              <!-- 用户名 -->
              <div class="u-name">
                <span><b>MMP</b></span>
              </div>
              <!-- 座右铭 -->
              <div class="u-motto">脸上笑嘻嘻</div>
            </div>
          </div>
          <div class="message">
            <div class="msg-item">
              <div class="num"><b>1</b></div>
              <div class="txt">今日回复</div>
            </div>
            <div class="msg-item">
              <div class="num"><b>1</b></div>
              <div class="txt">今日发布</div>
            </div>
            <div class="msg-item">
              <div class="num"><b>1</b></div>
              <div class="txt">今日获赞</div>
            </div>
          </div>
          <!-- 高斯背景 -->
          <div class="header-bg">
            <img width="100%" height="100%" src="../../../static/img/test.jpg" alt="">
          </div>
        </div>
        <!-- 下部分 -->
        <div class="footer">
          <div class="footer-list">
            <ul class="list-box">
              <li class="list">
                <span class="icon icon-fabu"></span>
                <span class="text">我的发布</span>
              </li>
              <li class="list">
                <span class="icon icon-shoucang"></span>
                <span class="text">我的收藏</span>
              </li>
              <li class="list">
                <span class="icon icon-hudongxiaoxi"></span>
                <span class="text">互动消息</span>
              </li>
              <li class="list">
                <span class="icon icon-zan"></span>
                <span class="text">鼓励作者</span>
              </li>
              <li class="list">
                <span class="icon icon-theme"></span>
                <span class="text">快速主题</span>
                <div class="quick-theme">
                  <div class="theme-item theme-red icon-clothes"></div>
                  <div class="theme-item theme-ylw icon-clothes"></div>
                  <div class="theme-item theme-blu icon-clothes"></div>
                  <div class="theme-item theme-grn icon-clothes"></div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="right" @click="hideLeftPanel()" :style="{transform:translateX,transition:animateTime}">
      </div>
    </div>
  </transition>
</template>
<style lang="less">
  @import "../../../static/css/common.less";
  .left-container{
    display: flex;
    flex-direction: row;
    position: fixed;
    top: 0;
    height:100%;
    width: 100%;
    transform:translateX(-100%);
    z-index: @zleftMenu;
    .left{
      position: fixed;
      height: 100%;
      width: 8.3rem;
      transform:translateX(-100%);
      box-shadow: 20/75rem 0 200/75rem rgba(0, 0, 0, 0.9);
      background: #fff;
      .header{
        width: 100%;
        background: rgba(255, 255, 255, 0.4);
        overflow: hidden;
        position: relative;
        height: 7.6rem;
        .edit{
          position:absolute;
          width: 100%;
          height: @menu-height;
          // background: #dedede;
          padding: 10/75rem 40/75rem;
          .edt-icon{
            float: right;
            font-size: 25px;
            line-height: @menu-height;
            color: @main-black;
          }
        }
        .head-box{
          width: 100%;
          height: 4.5rem;
          margin-top: 1rem;
          display: flex;
          flex-direction:column;
          align-items: center;
          .head-img{
            width: 3rem;
            height: 3rem;
            border-radius: 50%;
            // box-shadow: 0 0 30/75rem #000000 inset;
            .u-img{
              width: 3rem;
              height: 3rem;
              border-radius: 50%;
            }
          }
          .txt{
            // background: rgb(189, 189, 189);
            text-align: center;
            color:@main-black;
            .u-name{
              font-size: 18px;
              margin-top: 20/75rem;
            }
            .u-motto{
              font-size: 14px;
              margin-top: 10/75rem;
            }
          }
        }
        .message{
          display: flex;
          justify-content: space-around;
          height: 1.4rem;
          margin-top: 0.2rem;
          padding-top: 0.2rem;
          text-align: center;
          color:@main-black;
          background: rgba(0, 0, 0, 0.1);
          .msg-item{
            flex: 1;
            height: 0.9rem;
            &:not(:last-child){
              border-right: 1px solid @main-dkgray;
            }
            .num{
              font-size: 18px;
              line-height: 25/75rem;
            }
            .txt{
              margin-top: 0.1rem;
              font-size: 14px;
            }
          }
        }
        .header-bg{
          position: absolute;
          z-index: -1;
          transform: scale(1.2); 
          .filter(20);
          top:0;
          height: 7.48rem;
          width: 100%;
        }
      }
      .footer{
        width: 100%;
        height: calc(~'100% - 7.6rem');
        .footer-list{
          height: 100%;
          display: flex;
          align-items: flex-end;
          .list-box{
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            .list{
              width: 100%;
              height:1.5rem;
              line-height:1.5rem;
              position: relative;
              padding: 0 45/75rem;
              border-top: 1px solid @lineLightColor;
              display: flex;
              justify-content: flex-start;
              align-items: center;
              &:not(:last-child):active{
                background: @lineLightColor;
              }
              .icon{
                font-size: 20px;
                line-height:1.5rem;
                color: @main-gray;
              }
              .text{
                color: @main-black;
                font-size: 18px;
                margin-left: 45/75rem;
                line-height:1.5rem;
              }
              .quick-theme{
                line-height: 1.5rem;
                height: 0.8rem;
                width: 4.2rem;
                right: 0.2rem;
                position: absolute;
                display: flex;
                justify-content: space-around;
                .theme-item{
                  width: 0.8rem;
                  font-size: 20px;
                  border-radius: 50%;
                  line-height: 0.8rem;
                  text-align: center;
                  transition: @animateTime;
                  &.theme-red{
                    color: @main-red;
                    border: 1px solid @main-red;
                    &:active{
                      color: @main-dkred;
                      border: 1px solid @main-dkred;
                    }
                  }
                  &.theme-ylw{
                    color: @main-yellow;
                    border: 1px solid @main-yellow;
                    &:active{
                      color: @main-dkyellow;
                      border: 1px solid @main-dkyellow;
                    }
                  }
                  &.theme-blu{
                    color: @main-blue;
                    border: 1px solid @main-blue;
                    &:active{
                      color: @main-dkblue;
                      border: 1px solid @main-dkblue;
                    }
                  }
                  &.theme-grn{
                    color: @main-color;
                    border: 1px solid @main-color;
                    &:active{
                      color: @main-dkcolor;
                      border: 1px solid @main-dkcolor;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .right{
      position: fixed;
      left: 8.3rem;
      height: 100%;
      width: 1.7rem;
    }
  }
</style>
<script>
export default{
  props:['leftShow'],
  data(){
    return{
      leftPx: 0,//触摸移动X坐标累计
      eachLeftPx: 0,//触摸每次移动X坐标
      translateX: 0,//left菜单位移
      animateTime: '0.5s',//位移动画时间
    }
  },
  methods:{
    // 点击右边，隐藏侧滑
    hideLeftPanel(){
      this.animateTime = '0.5s';
      this.$emit('hideLeft');
    },
    //滑动左边菜单
    pressMove(evt){
      this.leftPx += evt.deltaX;
      this.eachLeftPx = evt.deltaX;
      this.translateX = 'translateX('+ this.leftPx +'px)';
      this.animateTime = '0s';
      evt.preventDefault();
    },

    //抬起手指
    touchEnd(){
      //滑动速度很快
      if(this.eachLeftPx < -5){
        this.$emit('hideLeft');  
        this.translateX = 'translateX(0px)';
        this.animateTime = '0.5s';
        // console.log(this.leftPx);
      };
      //整体位置判断是否隐藏
      if(this.leftPx <= -110){//滑动到左半页
        this.$emit('hideLeft');
        this.translateX = 'translateX(0px)';
        this.animateTime = '0.5s';
      }else{//滑倒右半页
        this.translateX = 'translateX(0px)';
        this.animateTime = '0.5s';
      };
      this.eachLeftPx = 0;
      this.leftPx = 0;
    }
  },
  // 监视
  watch:{
    // 监视是否显示左边菜单 调整菜单位置
    leftShow:function(val){
      if(val === true){
        this.translateX = 'translateX(0px)';
      }else{
        this.translateX = 'translateX(-100%)';
      }
    }
  }
}
</script>



