<template>
  <div>
    <div class="menu-list clearfix" v-for="(menuItem, index) in menuArr">
      <div class="category-title">
        <span>{{menuItem.title}}</span>
      </div>
      <div class="bg-box">
        <img class="menu-bg" :src="menuItem.backgroundImg" alt="" srcset="">
      </div>
      <div class="menu-box clearfix">
        <div v-for="(menuName, index) in menuItem.menuName" class="menu-block">
          <div class="menu-icon ripple" :style="{background:menuBgColor[getColorIndex()]}">{{menuName[0]}}</div>
          <div class="menu-name">{{menuName}}</div>
        </div>
      </div>
    </div>
    <pageFoot></pageFoot>
  </div>
</template>
<style lang="less">
  @import "../../../static/css/common.less";

  .menu-list{
      width: 100%;
      box-shadow: 0 0.1rem 0.2rem rgba(0,0,0,.2);
      position: relative;
      &:not(:first-child){
        margin-top: 0.7rem;
      }
      position: relative;
      .category-title{
        position: relative;
        z-index: 100;
        height: 1rem;
        width: 100%;
        background: rgba(0, 0, 0, 0.2);
        color: @main-black;
        font-size: 18px;
        text-align: center;
        color: #fff;
        line-height: 1rem;
      }
      .menu-box{
        width: 100%;
        min-height: 5rem;
        padding: 30/75rem 30/75rem 0.1rem;
        background: rgba(0,0,0,.2);
        .menu-block{
          float: left;
          width: 20%;
          text-align: center;
          margin-bottom: 0.3rem;
          user-select: none;
          .menu-icon{
            width: 1.5rem;
            height: 1.5rem;
            margin: 0 auto;
            background: #dedede;
            border-radius: 50%;
            box-shadow: 0 0 0.2rem rgba(255,255,255,.1);
            line-height: 1.5rem;
            text-align: center;
            font-size: 26px;
            font-weight: 900;
            color: rgb(241, 241, 241);
            transition: .5s;
            &:active{
              transform: scale(.9);
            }
          }
          .menu-name{
            color: @lineLightColor;
            margin-top: 0.1rem;
            opacity: .7;
          }
        }
      }
      .bg-box{
        position: absolute;
        width: 100%;
        height: 100%;
        margin-top: -1rem;
        overflow: hidden;
        .menu-bg{
          .filter(10);
          min-height: 100%;
          width: 100%;
          transform: scale(1.2);
        }
      }
    }
</style>
<script>
  import picAndTxt from '../../components/common/picAndTxt.vue'
  import pageFoot from '../../components/common/pageFoot.vue'
  
  export default{
    components: {
      'picAndTxt': picAndTxt,
      'pageFoot': pageFoot,
    },
    data() {
      return {
        menuArr:[
          {
            title:'历史朝代',
            backgroundImg:'static/img/test5.jpg',
            menuName:[
              '夏',
              '商',
              '周',
              '秦',
              '西汉',
              '唐朝',
              '宋朝',
              '元朝',
              '明朝',
              '清朝',
              '民国',
              '当代',
            ],
          },
          {
            title:'精选心语',
            backgroundImg:'static/img/test4.jpg',
            menuName:[
              '名人名言',
              '经典语录',
              '书籍名句',
              '电影台词',
              '小说摘抄',
              '散文美句',
              '网络流行',
              '伤感句子',
            ],
          },
          {
            title:'精选心语',
            backgroundImg:'static/img/test2.jpg',
            menuName:[
              '名人名言',
              '经典语录',
              '书籍名句',
              '电影台词',
              '小说摘抄',
              '散文美句',
              '散文美句',
              '散文美句',
              '散文美句',
              '散文美句',
              '网络流行',
              '伤感句子',
            ],
          }

        ],
        menuBgColor:[
          '#FF2A68',
          '#FF9500',
          '#FFDB4C',
          '#0BD318',
          '#5AC8FB',
          '#1AD6FD',
          '#C644FC',
          '#4A4A4A',
          '#898C90',
          '#EF4DB6',
          '#FF3B30',
          '#4CD964',
          '#007AFF',
          '#FF2D55',
          '#5BCAFF',
          '#FB2B69',
        ]
      }
    },
    methods:{
      getColorIndex:function(){
        var bgColorLen = this.menuBgColor.length;//颜色长度
        var radomIndex = parseInt(Math.random() * (bgColorLen - 1));//长度为基数，取随机数
        return radomIndex;
      }
    }
  }
</script>



