<template>
  <!-- <transition name="animate-router"> -->
    <div class="shudong-container clearfix">
      <shuDongTab :tabArr="shuDongTabArr"></shuDongTab>
    </div>
  <!-- </transition> -->
</template>
<style lang="less">
  @import "../../../static/css/common.less";
  .shudong-container{

  }
</style>
<script>
  import shuDongTab from '../../components/common/tab.vue'//tab组件
  export default {
    data(){
      return{
        //传到tab里面的数组，包含名字和该对应跳转链接
        shuDongTabArr:[
          {
            name: '深情表白',
            toUrl:'xiaoJiuJiu'
          },
          {
            name: '吐槽讨论',
            toUrl:'tuCaoTaoLun'
          }
        ],
      }
    },
    components: {
      "shuDongTab": shuDongTab,
    },
    methods:{
    }
  }
</script>



