<template>
  <transition name="animate-toTop">
    <div class="comfirm-container" v-if="visible">
      <div class="comfirm-box">
        <div class="txt">{{message}}</div>
        <div class="control">
          <div class="cancel" @click="cancelClick">取消</div>
          <div class="sure" @click="sureClick">确认</div>
        </div>
      </div>
    </div>
  </transition>
</template>
<style lang="less">
  @import "../../../static/css/common.less";
  .comfirm-container{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(185, 185, 185, 0.7);
    z-index: 666;
    display: flex;
    justify-content: center;
    color:#fff;
    font-size: .5rem;
    .comfirm-box{
      position: absolute;
      top: 7rem;
      width: 80%;
      border-radius: .1rem;
      text-align: center;
      background: -webkit-linear-gradient(left, rgba(24, 4, 4, 0.9) , rgba(51, 8, 8, 0.8) , rgba(24, 4, 4, 0.9) ); /* Safari 5.1 - 6.0 */
      background: -o-linear-gradient(right, rgba(24, 4, 4, 0.9) , rgba(51, 8, 8, 0.8) , rgba(24, 4, 4, 0.9) ); /* Opera 11.1 - 12.0 */
      background: -moz-linear-gradient(right, rgba(24, 4, 4, 0.9) , rgba(51, 8, 8, 0.8) , rgba(24, 4, 4, 0.9) ); /* Firefox 3.6 - 15 */
      background: linear-gradient(to right, rgba(24, 4, 4, 0.9) , rgba(51, 8, 8, 0.8) , rgba(24, 4, 4, 0.9) ); /* 标准的语法 */
      .txt{
        padding: .5rem;
        min-height: 2rem;
        border-bottom: 1.5/75rem solid rgba(255, 255, 255, 0.2);;
      }
      .control{
        display: flex;
        justify-content: space-between;
        text-align: center;
        height: 1.2rem;
        line-height: 1.2rem;
        .sure{
          border-left: 1.5/75rem solid rgba(255, 255, 255, 0.2);
        }
        .cancel,.sure{
          transition: @animateTime;
          flex: 1;
          &:active{
            background: rgba(24, 4, 4, 0.9) ;
          }
        }
      }
    }
  }
</style>
<script>
export default{
  data(){
    return{
      visible: false,
      message:'',
      cancel:'',
      sure:'',
    }
  },
  methods:{
      cancelClick(){
        this.visible = false;
        this.cancel();
      },
      sureClick(){
        this.visible = false;
        this.sure();
      },
  }
}
</script>



