        sliderinit: {
          currentPage: 0,//当前页码
          thresholdDistance: 50,//滑动判定距离
          thresholdTime: 1000,//滑动判定时间
          autoplay:4000,//自动滚动[ms]
          loop:true,//循环滚动
          direction:'horizontal',//方向设置，垂直滚动
          infinite:1,//无限滚动前后遍历数
          slidesToScroll:1,//每次滑动项数
        }
      }
    },
    methods: {
      // 轮播事件监听
      onTap (data) {
        this.$toast('点击');
      },
    }
  }
</script>



