<template>
  <div id="app">
    <!-- =============== 左边划出菜单 ================ -->
    <leftMenu :leftShow="leftShow" @hideLeft = "leftPanelHidden()" @showLeft = "leftPanelShow()" :style="leftShow == false ? 'transform: translateX(-100%);' : 'transform: translateX(0%);'"></leftMenu>
    <!-- =============== app整体页面 ================= -->
    <div class="clearfix right-content" :class="blurAll == true ? 'blurFilter' : 'blurFilterNo'">
      <!-- 界面顶部 -->
      <div class="top-content" :class="blurRouter == true ? 'blurFilter' : 'blurFilterNo'">
        <!-- 状态栏 -->
        <div class="nav-bar">
        </div>
        <!-- 顶部导航 -->
        <div class="nav-top">
          <!-- 头像 -->
          <div class="usr-head" @click="leftPanelShow()">
            <img class="head-img" src="../static/img/test.jpg" alt="">
          </div>
          <!-- 设置 -->
          <transition name="animate-opacity">
            <div class="setting" v-show="!searchPanel" :class="quickSettingShow === true ? 'active' : ''">
              <i class="icon-setting1" @click="quickSetting()"></i>
            </div>
          </transition>
          <!-- 勾勾 -->
          <transition name="animate-opacity">
            <div class="setting" v-show="searchPanel"  :class="quickSettingShow === true ? 'active' : ''">
              <i class="icon-gou2" @click="searchNow()"></i>
            </div>
          </transition>
          <!-- 搜索 -->
          <transition name="animate-opacity">
            <div class="search" v-show="!searchPanel">
              <i class="icon-search1" @click="searchShow()"></i>
            </div>
          </transition>
          <!-- 叉叉 -->
          <transition name="animate-opacity">
            <div class="search" v-show="searchPanel">
              <i class="icon-cha2" @click="searchShow()"></i>
            </div>
          </transition>
        </div>
        <!-- 快速设置 -->
        <quickSetting class="quick-setting-content" v-show="quickSettingShow"></quickSetting>
        <!-- 搜索界面 -->
        <search-panel :searchPanel="searchPanel" :searchClick="searchClick" @searchClickFalse="cgSearchClickFalse()" v-show="searchPanel"></search-panel>
      </div>
      <!-- 添加界面 -->
      <transition name="add-container">
        <div class="add-panel" v-show="addPanelShow">
          <!-- 上部分空白，预留功能区 -->
          <div class="top" @click="hideAddPanel()">
          </div>
          <!-- 三个菜单按钮 -->
          <div class="middle">
            <transition name="add-oneani">
              <div class="add-menu ripple" v-show="addPanelShow" @click="addRadiusFirst()">树&nbsp;洞</div>
            </transition>

            <transition name="add-twoani">
              <div class="add-menu ripple" v-show="addPanelShow" @click="addRadiusSecond()">句&nbsp;子</div>
            </transition>
            
            <transition name="add-threeani">
              <div class="add-menu ripple" v-show="addPanelShow" @click="addRadiusThird()">小功能</div>
            </transition>
          </div>
        </div>
      </transition>
      <!-- 路由渲染位置 -->
      <div class="route-res" :class="blurRouter == true ? 'blurFilter' : 'blurFilterNo'" v-finger:tap="routerTap">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </div>
      <!-- 底部菜单 -->
      <transition name="footMenu">
        <div class="foot-menu" v-show="!searchPanel">
          <router-link class="mune-list ripple" to="shouYe" @click.native="menuClick()">
            <i class="icon icon-heart"></i>
            <span>首页</span>
          </router-link>

          <router-link class="mune-list ripple" to="juZi" @click.native="menuClick()">
            <i class="icon icon-fenlei"></i>
            <span>排行分类</span>
          </router-link>

          <div class="mune-list" @click="addClick($event)">
            <span class="add">
              <i class="icon icon-add" :class="addRotate == true ? 'addRotate' : 'addRotateNo'"></i>
            </span>
          </div>

          <router-link class="mune-list ripple" to="shuDong" @click.native="menuClick()">
            <i class="icon icon-xingqiu"></i>
            <span>树洞秘密</span>
          </router-link>

          <router-link class="mune-list ripple" to="woDe" @click.native="menuClick()">
            <i class="icon icon-mine"></i>
            <span>我的</span>
          </router-link>
        </div>
      </transition>
      <!-- 添加按钮阴影 -->
      <transition name="footMenu">
        <span class="add-shadow" v-show="!searchPanel">
        </span>
      </transition>
    </div>
    
  </div>
</template>

<script>
  import '../static/js/flexible/flexible'// 自适应插件
  import '../static/js/flexible/flexible_css'// 自适应插件
  import home from '../src/components/shouYe/home.vue'//预加载首页
  import quickSetting from '../src/components/common/quickSetting.vue'//顶部快速设置
  import search from '../src/components/common/search.vue'//顶部快速设置
  import leftMenu from '../src/components/common/left.vue'//左边划出菜单
  
  export default {
    // 所有组件
     components: {
      'home': home,//预加载首页
      'quickSetting':quickSetting,//顶部快速设置
      'search-panel': search,//搜索
      'leftMenu':leftMenu,//左边划出
     },

    //  变量
     data() {
       return {
         addPanelShow: false,//  添加页面是否显示
         addRotate:false,//添加按钮是否旋转
         blurRouter:false,//是否高斯模糊 router中间
         blurAll:false,//是否高斯模糊 整体
         defaultHome: true,//默认加载的首页，讲道理只有打开app会看到 问题，点击菜单后刷新会重新出现
         quickSettingShow: false,//控制顶部快速设置显示
         searchPanel: false,//搜索界面显示控制
         searchClick:false,//点击搜索确认
         leftShow:false,//控制划出菜单显示
         routerMoveHieght:0,//上下拖动router滑动距离
         routerTranslateY:'translateY(0)',//router滑动距离
         routerAnimateTime:'0s',//router动画时间
         topFreshTxt:'下拉刷新',//顶部刷新显示文字
         topFreshIconRotateAngle:'rotate(-90deg)',//顶部刷新图标旋转角度
         topFreshIcon:'icon-back4',//刷新显示图标
         rotateAngle: 90,
       }
     },

     //所有的事件
     methods:{
       //  切换添加面板状态
       showAddPanel() {
         this.addPanelShow = true;//添加面板
       },
       hideAddPanel() {
         this.addPanelShow = false;//添加面板
        //  this.blurRouter = false;//高斯模糊
         this.addRotate = false;//添加按钮是否旋转
       },


       //----------底部菜单 添加 按钮点击-------------
       addClick(){
          this.addPanelShow = !this.addPanelShow;//添加面板
          this.addRotate = !this.addRotate;//添加按钮旋转
          // this.blurRouter = !this.blurRouter;//高斯模糊
          if(this.quickSettingShow === true){//点击菜单快速设置ti打开时，将其关闭p
            this.quickSettingShow = !this.quickSettingShow;
          }
          //  点击音效
          var audio = document.getElementById("bgMusicPaper");
          //播放(继续播放)
          audio.play();
       },
       //-----------其他底部菜单点击-----------
       menuClick(){
         //隐藏弹出的添加按钮菜单
         if(this.addPanelShow === true){
          this.addPanelShow = false;//添加面板
          this.blurRouter = false;//高斯模糊
          this.addRotate = false;//添加按钮是否旋转
         };

        //  点击音效
        var audio = document.getElementById("bgMusicPaper");
        //播放(继续播放)
        audio.play();

        //默认首页隐藏
        if(this.defaultHome === true ){
          this.defaultHome = false;
        }
        if(this.quickSettingShow === true){//点击菜单快速设置ti打开时，将其关闭p
          this.quickSettingShow = !this.quickSettingShow;
        }
       },

       //-----弹出圆形菜单点击---------
       addRadiusFirst(){//第一个菜单
        //  点击音效
        var audio = document.getElementById("bgMusicBubble");
        //播放(继续播放)
        audio.play();
       },
        addRadiusSecond(){//第二个菜单
        //  点击音效
        var audio = document.getElementById("bgMusicBubble");
        //播放(继续播放)
        audio.play();
       },
        addRadiusThird(){//第三个菜单
        //  点击音效
        var audio = document.getElementById("bgMusicBubble");
        //播放(继续播放)
        audio.play();
       },

      //  点击顶部快速设置按钮
      quickSetting(){
        this.quickSettingShow = !this.quickSettingShow;//快速设置tip
      },

      // 点击搜索按钮
      searchShow(){
        this.searchPanel = !this.searchPanel;//搜索界面
        this.quickSettingShow === true ? this.quickSettingShow = false : '';//隐藏快速设置
      },
      //确认搜索
      searchNow(){
        this.searchClick = true;
      },
      // 子组件回传函数，改变点击状态searchClick为false初始状态
      cgSearchClickFalse(){ 
        this.searchClick = false;
      },

      //触摸中间路由
      routerTap(){
        this.quickSettingShow === true ? this.quickSettingShow = false : '';//隐藏快速设置
      },

      //点击头像，显示左边划出页面
      leftPanelShow(){
        this.leftShow = true;//展示侧滑菜单
        this.quickSettingShow === true ? this.quickSettingShow = false : '';//隐藏快速设置
        // this.blurAll = true;//整体高斯模糊
      },
      leftPanelHidden(){
        this.leftShow = false;//不展示侧滑菜单
        // this.blurAll = false;//整体去除高斯模糊
      },

      // //所有的事件 结束
    }

  }
</script>

<style lang="less">
  @import "../static/css/icon.css";
  @import "../static/css/common.css";
  @import "../static/css/index.css";
</style>



