<template>
  <transition name="animate-toBottom">
    <div class="loading-content" v-show="visible">
        <img class="load-img" src="static/img/loading.gif" alt="" srcset="">
    </div>
  </transition>
</template>
<style lang="less">
  @import "../../../static/css/common.less";
  .loading-content{
    position: fixed;
    z-index: 9999;
    width: 100%;
    display: flex;
    justify-content: center;
    .load-img{
      position: absolute;
      top: 3.8rem;
      background: rgb(255, 255, 255);
      box-shadow: 0 0 .2rem rgba(0, 0, 0, 0.2);
      border-radius: 50%;
      padding: .2rem;
      transition: 0;
      transform: translate3d(0,0,0);
      animation:rotate .8s infinite linear;
      width: 1.2rem;
      height: 1.2rem;
    }

  }
</style>
<script>
export default{
  data(){
    return{
      visible: false,
      callback:'',
    }
  },
  methods:{
  },
  watch:{
    visible:function(val){
      if(val == true){
        this.callback();
      }
    }
  }
}
</script>



