<template>
  <!-- 文字：1.字体；2.位置；3.本身换行位置 -->
  <!-- 蒙版：1.位置;2.大小；3.模糊；4.透明度；5.颜色；6.圆角；6. -->
  <!-- 图片：1.模糊；2.高度；3.滤镜 -->
  <!-- <transition name="animate-router"> -->
    <div class="txt-list-container">
      <div class="txt-list clearfix" v-for="(item, index) in txtArr">
        <!-- 文本 -->
        <div class="txt-box clearfix">
          <div class="title">{{item.title}}</div>
          <div class="content">
            <div class="comment">
              <span class="name">{{item.hotWriter}}&nbsp;:&nbsp;</span>
              <span class="txt">{{item.hotComment}}</span>
            </div>
            <img v-if="item.imgSrc" class="img" :src="item.imgSrc">
          </div>
        </div>
        <!-- 操作面板 -->
        <div class="control clearfix">
          <!-- <div class="ct-item">
            <span>2018-11-11</span>
          </div> -->
          <div class="ct-item">
            <i class="icon-fenxiang"></i>
            <span>分享</span>
          </div>
          <div class="ct-item">
            <i class="icon-light"></i>
            <span>举报</span>
          </div>
          <div class="ct-item">
            <i class="icon-pinglun"></i>
            <span>评论 ( {{item.commentNum}} )</span>
          </div>
        </div>
      </div>
    </div>
  <!-- </transition> -->
</template>
<style lang="less">
  @import "../../../static/css/common.less";
  .txt-list-container{
    .txt-list{
      width: 100%;
      // height: 4.6rem;
      box-shadow: 0 0 0.2rem rgba(0,0,0,.1);
      &:not(:first-child){
        margin-top: 0.4rem;
      }
      .txt-box{
        width: 100%;
        padding: 30/75rem 30/75rem 20/75rem;
        background: #fff;
        border-bottom: 1.5/75rem solid @lineLightColor;
        display: flex;
        flex-direction: column;
        .title{
          font-size: .4rem;
          font-weight: bold;
          line-height: .6rem;
          color:@main-black;
        }
        .content{
          margin-top: .1rem;
          max-height: 2.2rem;
          line-height: .4rem;
          display: flex;
          justify-content: space-around;
          .comment{
            width: 100%;
            font-size: .38rem;
            line-height: .6rem;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 4;
            overflow: hidden;
            .name{
              color:@main-black;
            }
            .txt{
              color: @main-dkgray;
            }
          }
          .img{
            margin-left: .1rem;
            flex-shrink: 0;
            width: 3.2rem;
            height: 2.2rem;
            border-radius: .08rem;
          }
        }
      }
      .control{
        width: 100%;
        display: flex;
        justify-content: flex-end;
        padding: @paddingTwoside;
        align-items: center;
        height: 1rem;
        font-size: 24/75rem;
        color: #999999;
        background: #fff;
        .ct-item{
          i{
            font-size: 24/75rem;
          }
          &:active{
            transform: scale(.95);
            color: @main-dkgray;
          }
          margin-left: 0.3rem;
          transition: @animateTime;
          .active{
            color: @main-red;
          }
        }
      }
    }
  }

</style>
<script>
  import $ from 'jquery'
  export default {
    props: {
      txtListArr:Array
    },
    data() {
      return {
        txtArr:this.txtListArr
      }
    },
    mounted(){
      //判断评论喜欢是否缩写
      for(var i=0; i<this.txtListArr.length; i++){
        this.txtListArr[i].commentNum = this.calcNumber(this.txtListArr[i].commentNum);
      }
    },
    methods:{
      //判断评论喜欢是否缩写
      calcNumber(myNumber){
        if(myNumber > 1000){
          // 1000有余数
          if(myNumber % 1000 != 0){
            // 截取两位小数
            return Math.floor(myNumber / 10) / 100+ 'K+';
          // 1000无余数
          }else{
            // 截取两位小数
            return Math.floor(myNumber / 10) / 100+ 'K';
          }
        }
      },
    }
  }
</script>


