<template>
  <!-- 文字：1.字体；2.位置；3.本身换行位置 -->
  <!-- 蒙版：1.位置;2.大小；3.模糊；4.透明度；5.颜色；6.圆角；6. -->
  <!-- 图片：1.模糊；2.高度；3.滤镜 -->
  <!-- <transition name="animate-router"> -->
    <div>
      <div @click="toast" v-for="(txtAndPic, index) in txtAndPicArr" class="pic-txt clearfix">
        <!-- 背景 -->
        <div class="bg-box" :style="{height:txtAndPic.imgObj.boxHeight}">
          <img class="bg-img" :src="txtAndPic.imgObj.imgSrc" :style="txtAndPic.imgObj.imgStyle">
        </div>
        <!-- 蒙版 -->
        <div class="mask-box clearfix" :style="txtAndPic.maskObj.maskBoxStyle">
          <div class="mask" :style="txtAndPic.maskObj.maskStyle"></div>
        </div>
        <!-- 文本 -->
        <div class="txt-box" :style="txtAndPic.txtContentObj.txtBox">
          <div class="text" v-for="(txtObj, index) in txtAndPic.txtContentObj.txtObj" :style="txtObj.eachtxtObj">{{txtObj.text}}</div>
        </div>
        <!-- 操作面板 -->
        <div class="control">
          <div class="ct-item">
            <i class="icon-xiazai"></i>
            <span>下载</span>
          </div>
          <div class="ct-item">
            <i class="icon-fenxiang"></i>
            <span>分享</span>
          </div>
          <div class="ct-item">
            <i class="icon-heart active"></i>
            <span>喜欢 ( {{txtAndPic.like.number}} ) </span>
          </div>
          <div class="ct-item">
            <i class="icon-pinglun"></i>
            <span>评论 ( {{txtAndPic.comment.number}} ) </span>
          </div>
        </div>
      </div>
    </div>
  <!-- </transition> -->
</template>
<style lang="less">
  @import "../../../static/css/common.less";
  .pic-txt{
    position: relative;
    width: 100%;
    box-shadow: 0 0 0.2rem rgba(0,0,0,.1);
    &:not(:first-child){
      margin-top: 0.4rem;
      // border-top: solid 1px @lineLightColor;
    }
    .txt-box{
      position: absolute;
      padding: 30/75rem;
      .text{
        font-family: inherit;
      }
    }
    .mask-box{
      position: absolute;
      overflow: hidden;
      width: 100%;
      box-sizing: border-box;
      top:0;
      .mask{
        width: 100%;
      }
    }
    .bg-box{
      position: relative;
      width: 100%;
      overflow: hidden;
      text-align: center;
      background-size: 100%;
      .bg-img{
        width: 100%;
        vertical-align: middle;
      }
    }
    .control{
      display: flex;
      justify-content: flex-end;
      padding: @paddingTwoside;
      align-items: center;
      height: 1rem;
      font-size: 12px;
      color: #999999;
      background: #fff;
      .ct-item{
        i{
          font-size: 24/75rem;
        }
        &:active{
          transform: scale(.95);
          color: @main-dkgray;
        }
        margin-left: 0.3rem;
        transition: @animateTime;
        .active{
          color: @main-red;
        }
      }
    }
  }

</style>
<script>
  import $ from 'jquery'
  export default {
    props: {
      txtPicArr:Array
    },
    data() {
      return {
        txtAndPicArr:this.txtPicArr,
      }
    },
    mounted(){
      //判断评论喜欢是否缩写
      for(var i=0; i<this.txtAndPicArr.length; i++){
        this.txtAndPicArr[i].comment.number = this.calcNumber(this.txtAndPicArr[i].comment.number);
        this.txtAndPicArr[i].like.number = this.calcNumber(this.txtAndPicArr[i].like.number);
      }
    },
    methods:{
      //判断评论喜欢是否缩写
      calcNumber(myNumber){
        if(myNumber > 1000){
          // 1000有余数
          if(myNumber % 1000 != 0){
            // 截取两位小数
            return Math.floor(myNumber / 10) / 100+ 'K+';
          // 1000无余数
          }else{
            // 截取两位小数
            return Math.floor(myNumber / 10) / 100+ 'K';
          }
        }
      },
      toast(){
        //confirm
        this.$confirm('确认进行这种操作吗？',
          //取消
          function(){
            //toast
            this.$toast('取消操作');
          },
          //确认
          function(){
            this.$loading(function(){
              setTimeout(() => {
                this.visible = false;
              }, 1500);
            })
          },
        );
      },
    }
  }
</script>


