<template>
  <!-- <transition name="animate-router"> -->
    <div class="soul-word">
      <juZiTap :tabArr="juZiTabArr"></juZiTap>
    </div>
  <!-- </transition> -->
</template>
<script>
  import juZiTap from '../common/tab.vue';//页脚
  import pageFoot from '../common/pageFoot.vue';//页脚

  export default {
    components: {
      'pageFoot': pageFoot,
      'juZiTap': juZiTap,
    },
    data(){
      return{
        juZiTabArr:[
          {
            name: '热门',
            toUrl:'jzReMen'
          },
          {
            name: '最新',
            toUrl:'jzZuiXin'
          },
          {
            name: '排行',
            toUrl:'jzPaiHang'
          },
          {
            name: '分类',
            toUrl:'jzFenLei'
          },
        ],
        
      }
    },

  }
</script>
<style lang="less">
  @import "../../../static/css/common.less";
  .soul-word{
    background: rgba(0,0,0,.05);
    position: relative;
    
  }
</style>


