<template>
    <div class="tab-container" id="tap">
      <transition name="animate-toTop">
        <div class="tab-fixed">
          <ul class="tab-box">
            <li class="item" v-for="(tabMenu, index) in tabTxt" @click = "changeTab(index)">
              <router-link class="router" :to="tabMenu.toUrl">{{tabMenu.name}}</router-link>
            </li>
            <div class="tab-bg"></div>
            <div class="bg-block" :style="{width:activeBgWidth+'%',transform:activeBgPosition}"></div>
          </ul>
        </div>
      </transition>
      <div class="tab-routerView">
        <router-view></router-view>
      </div>
    </div>
</template>
<style lang="less">
  @import "../../../static/css/common.less";
  .tab-container{
    .tab-fixed{
      position: fixed;
      top: @statusBar-height + @menu-height;
      width: 100%;
      z-index: 50;
      box-shadow: 0 .05rem .1rem rgba(0,0,0,.1);
      &.hide{
        top:-2rem;
      }
      .tab-box{
        display: flex;
        justify-content: space-around;
        
        .item{
          height: 1rem;
          line-height: 1rem;
          flex:1;
          text-align: center;
          font-size: 16px;
          user-select: none;
          .router{ 
            width: 100%;
            height: 100%;
            display: inline-block;
            color:@main-black;
            text-decoration: none;
          }
        }
        .bg-block{
          height: 1rem;
          position: absolute;
          left: 0;
          z-index: -1;
          background: @light-gray;
          transition: @animateTime;
          opacity: 0.9;
        }
        .tab-bg{
          position: absolute;
          width: 100%;
          height: 1rem;
          z-index: -2;
          background: @lineLightColor;
          opacity: 0.95;
        }
      }
    }
    .tab-routerView{
      margin-top: 1rem;
      position: relative;
      z-index: 40;
      background: rgba(0,0,0,.05);
    }
  }
</style>
<script>
  export default {
    props: {
      tabArr:Array
    },
    data() {
      return {
        tabTxt:this.tabArr,//调用的地方传进来的数组，以及跳转的连接名字 像这样name: '每日心语',toUrl:'meiRiXinYu'
        activeindex: 0, //激活菜单的index
        activeBgWidth: '', //菜单背景大小
        activeBgPosition: 'translateX(0%)', //菜单背景位置
      }
    },
    methods:{
      changeTab(tabIndex){
        this.activeindex = tabIndex;
        this.activeBgPosition = 'translateX('+ tabIndex * 100 +'%)';
        // console.log(this.activeBgPosition)
      }
    },
    mounted() {//挂在之后
      this.activeBgWidth = 100 / this.tabTxt.length;//计算tab背景宽度
    }
  }
</script>



