<template>
  <transition name="animate-toTop">
    <div class="quick-setting">
      <ul class="list-box">
        <li class="list" @click="cgNightMode()">
            <a href="javascript:void(0)">
              <i class="icon" :class="nightMode == true ? 'icon-moon' : 'icon-sun'"></i>
              <span class="text">{{nightMode == true ? " 夜晚模式" : "白天模式"}}</span>
            </a>
        </li>
        <li class="list" @click="cgSoundMode()">
          <a href="javascript:void(0)">
            <i class="icon" :class="soundMode == true ? 'icon-no-sound1' : 'icon-sound'"></i>
            <span class="text">{{soundMode == true ? "关闭音效" : "打开音效"}}</span>
          </a>
        </li>
        <li class="list" @click="cgPicMode()">
          <a href="javascript:void(0)">
            <i class="icon" :class="picMode == true ? 'icon-no-pic' : 'icon-no-pic'"></i>
            <span class="text">{{picMode == true ? "无图模式" : "有图模式"}}</span>
          </a>
        </li>
        <li class="list">
          <a href="javascript:void(0)">
            <i class="icon icon-fankui"></i>
            <span class="text not-spacing">反馈/建议</span>
          </a>
        </li>
        <li class="list">
          <a href="javascript:void(0)">
            <i class="icon icon-setting1"></i>
            <span class="text">更多设置</span>
          </a>
        </li>
      </ul>
      <!-- 纸张翻页音效 -->
    <audio id="bgMusicPaper" :src="soundMode === true ? 'static/sounds/paper.mp3' : ''"></audio>
    <!-- 气泡音效 -->
    <audio v-show="soundMode" id="bgMusicBubble" :src="soundMode === true ? 'static/sounds/bubble.mp3' : ''"></audio>
    </div>
  </transition>
</template>
<style lang="less">
  @import "../../../static/css/common.less";
  .quick-setting{
    width:4.5rem;
    background: rgba(255,255,255,.96);
    box-shadow: 0 0.1rem 0.2rem rgba(0, 0, 0, 0.3);
    border-radius: 0 0 0.1rem 0.1rem;
    .list-box{
      .list{
        padding: 0 0.4rem;
        transition: @animateTime;
        &:not(:last-child){
          border-bottom: 1px solid @lineLightColor;
        }
        &:active{
          background: @lineLightColor;
        }
        a{
          line-height: 1.2rem;
          font-size: 16px;
          color: @main-black;
          text-decoration: none;
          position: relative;
          .icon{
            position: absolute;
            padding-top: 0.02rem;
            line-height: 1.2rem;
            color: @main-gray;
          }
          .text{
            margin-left:0.7rem;
            letter-spacing: 0.05rem;
            &.not-spacing{
              letter-spacing: 0;
            }
          }
        }
      }
    }
  }
</style>
<script>
  export default{
    data(){
      return{
        nightMode:false,//夜间模式
        nightTxt:'白天模式',
        nightIcon:'icon-sun',
        picMode:true,//有图模式
        soundMode:true,//有声模式
      }
    },
    methods:{
      cgNightMode(){//夜间模式
        this.nightMode = !this.nightMode;
      },
      cgPicMode(){//有图模式
        this.picMode = !this.picMode;
      },
      cgSoundMode(){//有声模式
        this.soundMode = !this.soundMode;
      },
    }
  }
</script>


