<template>
  <!-- <transition name="animate-router"> -->
    <div class="home-container">
      <homeTab :tabArr="homeTabArr"></homeTab>
    </div>
  <!-- </transition> -->
</template>
<style lang="less">
  @import "../../../static/css/common.less";
</style>
<script>
  import homeTab from '../../components/common/tab.vue'//tab组件
  import $ from 'jquery';
  
  export default{
    components: {
      'homeTab': homeTab,
    },
    data(){
      return{
        //传到tab里面的数组，包含名字和该对应跳转链接
        homeTabArr:[
          {
            name: '心灵走廊',
            toUrl:'xinLingZouLang'
          },
          {
            name: '话题讨论',
            toUrl:'huaTiTaoLun'
          },
          {
            name: '最近精句',
            toUrl:'zuiJinJingJu'
          }
        ],

      }
    }
  }
</script>



